body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header__container{width:100vw;max-width:280px}@media all and (min-width: 768px){.header__container{max-width:728px;width:500px}}.header__container .header-img{width:100%}.main__container{display:flex;flex-direction:column;align-items:center;justify-content:center;max-width:280px}@media all and (min-width: 768px){.main__container{max-width:728px}}@media all and (min-width: 1200px){.main__container{max-width:1100px}}.main__container .characterlist__container{list-style:none;display:grid;grid-template-columns:1fr;grid-gap:20px;max-width:280px;width:100%;padding:0px;align-items:center}@media all and (min-width: 768px){.main__container .characterlist__container{grid-template-columns:1fr 1fr 1fr;max-width:728px}}@media all and (min-width: 1200px){.main__container .characterlist__container{grid-template-columns:1fr 1fr 1fr 1fr;max-width:1100px}}.main__container .characterlist__container .charactercard__firstcontainer{width:100%;background-color:transparent;border-radius:5%;border:#a5ca4a solid 4px}.main__container .characterlist__container .charactercard__firstcontainer:hover{border-style:dashed}.link{text-decoration:none}.text-input-error{color:white}.characterdetail__container{background-color:transparent;border-radius:5%;border:#a5ca4a solid 4px;max-width:280px;width:100%;display:flex;flex-direction:row;flex-wrap:wrap;padding:5px}@media all and (min-width: 768px){.characterdetail__container{max-width:400px;width:100%}}.characterdetail__container .characterdetail__container--image{width:50%;order:0}.characterdetail__container .characterdetail__container--image img{width:100%}.characterdetail__container .characterdetail__container--info{display:flex;flex-direction:column;order:1;align-items:center;margin-left:5px;width:40%;justify-content:space-around}.characterdetail__container .characterdetail__container--info .button-link{text-decoration:none;display:block}.characterdetail__container .characterdetail__container--info .button-link .back-button{font-size:10px;border-radius:10%;background-color:black;color:#a5ca4a;outline:none;border:#a5ca4a solid 2px}.characterdetail__container .characterdetail__container--info h2,.characterdetail__container .characterdetail__container--info span{display:block;margin:0px;font-size:14px;color:white}.charactercard__secondcontainer{max-width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:10px}.charactercard__secondcontainer .charactercard__container--image{width:60%}.charactercard__secondcontainer .charactercard__container--image img{width:100%}.charactercard__secondcontainer h2,.charactercard__secondcontainer h3{font-family:"Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode";text-transform:uppercase;margin:0px;font-size:16px;color:white}@media all and (min-width: 768px){.charactercard__secondcontainer h2,.charactercard__secondcontainer h3{font-size:12px}}form{max-width:220px}@media all and (min-width: 768px){form{max-width:400px;width:300px}}form input{background-color:transparent;color:#a5ca4a;border:#a5ca4a solid 2px;border-radius:8px;width:100%;height:25px}::-webkit-input-placeholder{color:#a5ca4a;padding:2px}:-ms-input-placeholder{color:#a5ca4a;padding:2px}::-ms-input-placeholder{color:#a5ca4a;padding:2px}::placeholder{color:#a5ca4a;padding:2px}.App{text-align:center;background-repeat:repeat;background-color:black;background-size:cover;max-width:100%;display:flex;flex-direction:column;align-items:center;padding:20px;min-height:100vh}.App p{color:white;font-size:16px}

