.main__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 280px;

  @media all and (min-width: 768px) {
    max-width: 728px;
  }

  @media all and (min-width: 1200px){
      max-width: 1100px;
  }

  & .characterlist__container {
    list-style: none;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 20px;
    max-width: 280px;
    width: 100%;
    padding: 0px;
    align-items: center;

    @media all and (min-width: 768px) {
      grid-template-columns: 1fr 1fr 1fr;
      max-width: 728px;
    }

    @media all and (min-width: 1200px) {
      grid-template-columns: 1fr 1fr 1fr 1fr;
      max-width: 1100px;
    }

    & .charactercard__firstcontainer {
      width: 100%;
      background-color: transparent;
      border-radius: 5%;
      border: rgb(165, 202, 74) solid 4px;

      &:hover {
        border-style: dashed;
      }
    }
  }
}

.link {
  text-decoration: none;
}

.text-input-error{
  color: white;
}
